<template>
  <el-container id="edm-detail-page">
    <div class="main-container">
      <el-button type="success" @click="onChooseCollectionClick">
        Choose Collection
      </el-button>
      <div class="input-container">
        <div class="input-item">
          <p class="label">Email:</p>
          <el-input v-model="email" clearable> </el-input>
        </div>
        <div class="input-item">
          <p class="label">Subject:</p>
          <el-input v-model="subject" clearable> </el-input>
        </div>
        <div class="input-item">
          <p class="label">Brand Name:</p>
          <el-input v-model="brandName" clearable> </el-input>
        </div>
        <div class="input-item">
          <p class="label">Text:</p>
          <el-input type="textarea" rows="3" v-model="message" clearable>
          </el-input>
        </div>
      </div>

      <div class="template-container">
        <component
          :is="templateComponent"
          :data="{ brandName, message }"
        ></component>
      </div>

      <div class="fabrics-container" v-if="collections.length">
        <el-row>
          <el-col v-for="(item, index) in collections" :key="index" :span="12">
            <div class="card">
              <div
                class="image"
                :style="{
                  backgroundImage: `url('${formatImageUrl(
                    item.fabric.image_url
                  ) + '@150-150'}')`
                }"
              ></div>
              <div class="right-container">
                <h3>{{ item.fabric.name }}</h3>
                <p>{{ item.fabric.real_composition }}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>

      <div class="button-container">
        <el-button type="success" @click="onSaveClick">
          Save EDM
        </el-button>
        <el-button type="primary" :disabled="!canSave" @click="onSendClick">
          Send EDM
        </el-button>
      </div>
    </div>

    <EDMDialog ref="dialog" @select="onDialogSelect"></EDMDialog>
  </el-container>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  computed,
  watch,
  onMounted,
  Ref
} from "vue";
import { useStore } from "vuex";

import Template1 from "./templates/Template1.vue";
import Template2 from "./templates/Template2.vue";
import Template3 from "./templates/Template3.vue";
import EDMDialog from "./EDMDialog.vue";
import {
  GetEdm,
  PostUpdateEdm,
  PostSendEdm,
  GetCollection
} from "@/typings/store";
import { formatImageUrl, loading } from "@/utils";
import { EdmParams, EdmResponse, SendEdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { iCreateDialog } from "@/typings/component";
import { createEDMHtml } from "@/utils/createEDMHtml";

const Component = defineComponent({
  name: "EDMDetail",
  components: {
    Template1,
    Template2,
    Template3,
    EDMDialog
  },
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup: function(props) {
    const store = useStore();
    const dialog: Ref<iCreateDialog | null> = ref(null);

    const { id } = toRefs(props);
    const email = ref("");
    const subject = ref("");
    const brandName = ref("");
    const message = ref("");
    const collectionId = ref(-1);
    const collections = ref([]);
    const templateComponent = ref("");
    const canSave = ref(true);
    const isFirst = ref(false);

    const detail = computed(() => store.state.edm.detail);
    const tempCollections = computed(() => store.state.collection.detail);

    watch([subject, brandName, message, collectionId], (items, prevItems) => {
      const hasDiff = items.some((item, index) => item !== prevItems[index]);
      if (hasDiff) {
        if (!isFirst.value) {
          return (isFirst.value = true);
        }
        canSave.value = false;
      }
    });

    const getCollectionId = (detail: EdmResponse) => {
      if (!detail) return -1;
      return detail.chosen_collection;
    };

    const fetchCollection = async (id: number) => {
      await store.dispatch(GetCollection, id);
      collections.value = tempCollections.value.collection_fabrics;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetEdm, id.value);
        templateComponent.value = `Template${detail.value.chosen_template ||
          "1"}`;
        subject.value = detail.value.subject;
        brandName.value = detail.value.recipient_brand;
        message.value = detail.value.message;

        const _collectionId = getCollectionId(detail.value);
        if (_collectionId !== -1 && _collectionId !== null) {
          collectionId.value = _collectionId;
          await fetchCollection(_collectionId);
        }

        canSave.value = true;
      } finally {
        loadingInstance.close();
      }
    };

    const save = async () => {
      const params: EdmParams = {
        subject: subject.value,
        recipient_brand: brandName.value,
        message: message.value,
        html_message: createEDMHtml(collections.value),
        creator: 1, // TODO 確認之後是否還要帶
        chosen_collection:
          collectionId.value === -1 ? null : collectionId.value,
        chosen_template: detail.value.chosen_template
      };
      const data = await store.dispatch(PostUpdateEdm, {
        id: id.value,
        params
      });
      ElNotification({
        title: "Success",
        type: "success",
        message: `Save EDM ${data.subject} successful.`
      });
      await init();
    };

    onMounted(async () => await init());

    const onChooseCollectionClick = () => {
      if (!dialog.value) return;
      dialog.value.open();
    };

    const onDialogSelect = async (id: number) => {
      collectionId.value = id;
      await fetchCollection(id);
    };

    const onSaveClick = async () => {
      await save();
    };

    const onSendClick = async () => {
      const params: SendEdmParams = {
        recipient_list: email.value.split(",").map(it => it.trim()),
        mail: id.value,
        sender: 1
      };
      try {
        await store.dispatch(PostSendEdm, params);
        ElNotification({
          title: "Success",
          type: "success",
          message: `Send e-mail successful.`
        });
      } finally {
        email.value = "";
      }
    };

    return {
      dialog,
      email,
      subject,
      brandName,
      message,
      templateComponent,
      collections,
      detail,
      canSave,

      onChooseCollectionClick,
      onDialogSelect,
      onSaveClick,
      onSendClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

