const createCollections = (collections: CollectionFabrics[]) => {
  const data = collections.reduce((prev, item, index) => {
    console.log(prev);
    if (!(index % 2)) {
      // @ts-ignore
      prev.push([item]);
    } else {
      // @ts-ignore
      (prev[prev.length - 1] as []).push(item);
    }
    return prev;
  }, []);

  return `
    <table
      border="0"
      cellpadding="0"
      cellspacing="0"
      width="100%"
    >
      ${data.map(
        item => `
          <tr>
            <td width="355" valign="top">
              ${createImage(item[0])}
            </td>
            <td style="font-size: 0; line-height: 0;" height="10">
              &nbsp;
            </td>
            <td width="355" valign="top">
              ${item[1] ? createImage(item[1]) : ""}
            </td>
          </tr>
          <tr>
            <td style="font-size: 0; line-height: 0;" height="30">
              &nbsp;
            </td>
          </tr>
        `
      )}
    </table>
  `;
};

